import React, {useEffect} from 'react'
import marked from 'marked'
import tocbot from 'tocbot';
import hljs from "highlight.js";

import './index.css'
import './github-markdown.css'
import 'highlight.js/styles/monokai-sublime.css';
import { Link } from 'react-router-dom';



function Article() {

    const data = {
        "Category": {
            "id": 8,
            "name": "计算机网络",
            "count": 3
        },
        "ID": 5,
        "CreatedAt": "2021-08-11T22:45:37+08:00",
        "UpdatedAt": "2021-08-11T22:45:37+08:00",
        "DeletedAt": null,
        "title": "11111111111",
        "cid": 8,
        "desc": "",
        "context": "# 网络层——控制平面\n\n### 路由选择算法\n\n**用图来表示路由选择，图的最短路径表示路由选择的最小开销**\n\n> **路由选择算法的第一种分类**\n\n* **集中式路由选择算法**\n  **用完整的链路信息来计算出最短路径，要求已知所有网络的完整信息**\n  **也被称为链路状态算法(LS)**\n* **分散式路由选择算法**\n  **路由器以迭代，分布式的方式计算出最低开销路径，不了解所有的网络   信息，如距离向量算法(DV)**\n\n> **路由选择算法的另外几种分类**\n\n* **静态/动态**\n* **负载敏感/负载迟钝**\n  **当今大数因特网的路由选择算法是负载迟钝的**\n\n> **链路状态路由选择算法**\n\n**图求最短路径的算法，权值是链路的开销**\n\n> **距离向量路由选择算法**\n\n`dx(y) = minv{c(x,u) + dv(y)}`\n\n> **LS 与 DV  的比较**\n\n* **报文的复杂性**\n  **LS要发送O（|N||E|)个报文，而且任何一条链路的开销改变时，必须向所有的节点发送新的链路开销，DV只在直接相连的邻居之间交换报文，而且链路开销改变时，除非导致最低开销也改变，才传播新的链路开销**\n* **收敛速度**\n  **LS是O(N^2)算法，而DV收敛较慢，且可能发生无穷计数等问题**\n* **健壮性**\n  **当路由器发送错误，LS向其它连接的链路广播不正确的开销，但是节点仅计算自己的转发表，在一定程度上是分离的，而DV算法会传递给它的邻居，它的邻居在下一个迭代会给另一个邻居，可能错误的开销会扩散到整个网络。**\n\n### 因特网中自治系统内部的路由选择：`OSPF`\n\n> **自治系统(AS)**\n\n**解决因特网中路由选择算法中规模巨大，ISP管理自治的问题**\n\n**一组AS通常由处于相同管理控制下的路由器组成。**\n\n**在相同AS中的路由器都运行着相同的路由选择算法并且拥有彼此的信息。**\n\n> **开放最短路优先**`(OSPF)`\n\n`OSPF`是一种链路状态协议，它使用洪泛链路状态信息和Dijkstra最低开销路径算法，每台自制系统中的路由器有一张完整的自治系统的拓扑图，用于计算自己到根节点的最短路径，协议规定周期性的广播链路状态。\n\n> `OSPF`的优点\n\n* **安全 ： 能够鉴别**`OSPF`路由器之间的交换\n* **多条相同开销的路径 ：当开销相同时，**`OSPF`允许使用多条路径\n* **对单播和多播路由选择的综合支持**\n* **支持在单个AS中的层次结构**\n\n### ISP之间的路由选择：`BGP`\n\n`边界网关协议`所有的AS运行相同的AS间的路由选择协议\n\n> `BGP`的作用\n\n**作为一种AS之间的路由选择协议，**`BGP`完成了以下任务\n\n1. **从邻居AS获得前缀的可达性信息，**`BGP`允许每个子网向因特网的其余部分通告它的存在。\n2. **确定到该前缀的“最好的”路由。**\n\n> **通告**`BGP`路由信息\n\n1. `BGP`连接：每对路由器通过使用179端口半永久`TCP`连接交换路由选择信息，每条直接连接以及所有通过该连接发送的`BGP`报文。\n   * `外部BGP连接`：跨越两个AS的`BGP`连接。\n   * `内部BGP连接`：相同AS中的两台路由器之间的`BGP`会话。\n\n> **确定最好的路由**\n\n1. **热土豆路由选择：**\n   **对于路由器，尽可能快地将分组送出其AS(用可能的最低开销)，而不担心其AS外部到目的地的余下部分的开销。**\n2. **实际的路由选择算法：**\n   * **路由被指派一个本地偏好值，选择最高本地偏好值。**\n   * **从余下的路由中，将选择具有最短的AS-PATH的路由**\n   * **从余下的路由中，使用热土豆路由选择**\n   * **如果仍留下多条路由，该路由使用**`BGP`标识符选择路由\n\n> `IP`任播\n\n### `SDN`控制平面\n\n> `SDN`体系结构的4个关键特征\n\n* **基于流的转发**\n* **数据平面与控制平面分离**\n* **网络控制功能**\n* **可编程的网络**\n\n### `ICMP`因特网控制报文协议\n\n**被主机和路由器用来彼此沟通网络层的信息。**\n",
        "img": "http://qxb2o0r7y.hb-bkt.clouddn.com/Fpz9oRV3T94YHQd8VTxO6EaM5bk2"
    }

    const html = marked(data.context)

    marked.setOptions({
        renderer: new marked.Renderer(),
        highlight: function (code) {
            return hljs.highlightAuto(code).value;
        },
        gfm: true,
        pedantic: false,
        sanitize: false,
        tables: true,
        smartLists: true, // 使用比原生markdown更时髦的列表
        smartypants: false, // 使用更为时髦的标点
    })

    useEffect(() => {
        tocbot.init({
            tocSelector: '.Catelog',
            contentSelector: '.markdown-body',
            headingSelector: 'h1, h2, h3 , h4 , h5 , h6',
            hasInnerContainers: true,
            scrollSmooth: true,
            collapseDepth: true,
            scrollSmoothOffset: -80,
        });

    })



    return(
        <div className='Articlebody' >
            <div className="Articleside">
                <div className = 'Catelog'></div>
                <Link to ='/notes'>
                    <button className='return'>返回</button>
                </Link>

            </div>
            <div className='Articlemain'>
                <div dangerouslySetInnerHTML={{ __html: html }} className="markdown-body"></div>
            </div>
        </div>
    )
}

export default Article
